import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Link } from "react-router-dom";
import cookie from "react-cookies";
import styles from "../css/Mypage.module.css";
import ItemList from "../components/ItemList";
import { rootUrl } from "..";

const Mypage = () => {
  const [userName, setUserName] = useState("");
  const [userItem, setUserItem] = useState([]);
  const [firstIndex, setFirstIndex] = useState(0);
  const [lastIndex, setLastIndex] = useState(6);
  const [page, setPage] = useState(1);

  //  
  const nextPage = () => {
    if (page === Math.ceil(userItem.length / 6)) {
      return alert("  .");
    }
    setFirstIndex((prev) => (prev += 6));
    setLastIndex((prev) => (prev += 6));
    setPage((prev) => (prev += 1));
  };
  //  
  const prevPage = () => {
    if (page === 1) {
      return alert("  ");
    }
    setFirstIndex((prev) => (prev -= 6));
    setLastIndex((prev) => (prev -= 6));
    setPage((prev) => (prev -= 1));
  };

  const { id } = useParams();
  useEffect(() => {
    window.scrollTo(0, 0);
    getUserData();
  }, []);

  //  
  const getUserData = async () => {
    const response = await fetch(`${rootUrl}/user/${id}`);
    if (response.status === 200) {
      await response.json().then((data) => {
        const { user } = data;
        setUserName(user.name);
        setUserItem(user.item.reverse());
      });
    }
  };
  return (
    <div className={styles.Mypage}>
      <h1 className={styles.title}>{userName + " "}</h1>
      {/*   */}
      {cookie.load("loggedInUser") === id ? (
        <Link to={`/user/${id}/edit`}>
          <br />
          <h2 className={styles.userEdit}> </h2>
        </Link>
      ) : null}
      <div className={styles.userform}>
        <button className={styles.btns} onClick={prevPage}>
          {"<"}
        </button>
        <button className={styles.btns} onClick={nextPage}>
          {">"}
        </button>
        {/*        */}
        <ul className={styles.useritems}>
          {userItem.length !== 0 ? (
            userItem.slice(firstIndex, lastIndex).map((item) => (
              <li key={item._id} className={styles.item}>
                <ItemList
                  id={item._id}
                  imgUrl={item.imgUrl[0]}
                  title={item.title}
                />
              </li>
            ))
          ) : (
            <li className={styles.noitem}> </li>
          )}
        </ul>
      </div>
    </div>
  );
};

export default Mypage;
