import { useEffect, useState } from "react";
import styles from "../css/Messages.module.css";
import io from "socket.io-client";
import { useParams } from "react-router-dom";
import cookie from "react-cookies";
import { rootUrl } from "..";

const Messages = () => {
  // 
  const [chatList, setChatList] = useState([]);

  // 
  const { id, id2, itemid } = useParams();

  const crateRoom = async () => {
    //id2 itemid   
    if (id2 === undefined || itemid === undefined) {
      return;
    }
    const response = await fetch(`${rootUrl}/message/${id}`, {
      method: "post",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify({
        id,
        id2,
        itemid,
      }),
    });
    loadRooms();
  };

  //  
  const loadRooms = async () => {
    const response = await fetch(`${rootUrl}/message/${id}`);
    if (response.status === 200) {
      await response.json().then((data) => {
        setChatList(data.user.chat);
      });
    }
  };

  useEffect(() => {
    window.scrollTo(0, 0);
    crateRoom();
    loadRooms();
  }, []);

  const [chatLog, setChatLog] = useState([]);
  const [roomId, setRoomId] = useState("");
  const [roomTitle, setRoomTitle] = useState("");

  const [chatStart, setChatStart] = useState(false);
  const [ws, setWs] = useState();

  // 
  const openChatRoom = (event) => {
    setRoomTitle(event.target.innerText.replace("  ", ""));
    chatList.forEach((room) => {
      const chatLogArr = [];
      if (room._id === event.target.id) {
        setRoomId(room._id);
        if (room.messages !== undefined) {
          room.messages.forEach((id) => {
            if (id[0] === cookie.load("loggedInUser")) {
              chatLogArr.push(` : ${id[1]}`);
            } else {
              chatLogArr.push(` : ${id[1]}`);
            }
            setChatLog([...chatLogArr]);
          });
        } else {
          setChatLog([]);
        }
      }
    });
    setChatStart(true);
  };

  // 
  const sendMessage = () => {
    const message = document.getElementById("message");
    if (message.value === "") {
      return;
    }
    setChatLog((prev) => [...prev, ` : ${message.value}`]);
    ws.emit("send_message", { message: message.value });
    setTimeout(() => (message.value = ""), 10);
  };
  //  
  const writeMessage = (msg) => {
    setChatLog((prev) => [...prev, ` : ${msg}`]);
  };

  // 
  const closeChatRoom = () => {
    setChatStart(false);
    setChatLog([]);
    setRoomTitle("");
    ws.disconnect();
  };

  //   
  useEffect(() => {
    if (chatStart) {
      setWs(
        io.connect(
          `${rootUrl}?userName=${cookie.load("userName")}&&userId=${cookie.load(
            "loggedInUser"
          )}&&roomId=${roomId}`
        )
      );
    }
  }, [chatStart]);

  // 
  useEffect(() => {
    ws?.on("receive_message", (msg) => {
      writeMessage(msg);
    });
  }, [ws]);

  //  
  useEffect(() => {
    const chatDiv = document.getElementById("Chatlog");
    chatDiv?.scrollTo(999, 999);
  }, [chatLog]);

  return (
    <div className={styles.Message}>
      <h1 className={styles.title}></h1>
      <div className={styles.messageForm}>
        <br />
        <h2>{roomTitle === "" ? " " : roomTitle}</h2>
        <ul className={styles.chatList}>
          {chatStart ? (
            <li className={styles.room}>
              <h2 onClick={closeChatRoom} className={styles.out}>
                
              </h2>
              <div id="Chatlog" className={styles.chatlog}>
                <ul className={styles.messages}>
                  {chatLog?.map((message, index) => (
                    <li className={styles.message} key={index}>
                      {message}
                    </li>
                  ))}
                </ul>
              </div>
              <input
                onKeyPress={(event) =>
                  event.key === "Enter" ? sendMessage() : null
                }
                className={styles.sendMessage}
                type="text"
                id="message"
              />
              <button onClick={sendMessage} className={styles.sendBtn}>
                
              </button>
            </li>
          ) : chatList?.length > 0 ? (
            chatList.map((room) => (
              <li
                onClick={openChatRoom}
                key={room._id}
                id={room._id}
                className={styles.rooms}
              >
                {room.item.title}  
              </li>
            ))
          ) : (
            <div>
              <h1 className={styles.noChat}> </h1>
            </div>
          )}
        </ul>
      </div>
    </div>
  );
};

export default Messages;
