import { useEffect, useState } from "react";
import { useParams, Link, useNavigate } from "react-router-dom";
import cookie from "react-cookies";
import styles from "../css/Item.module.css";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Modal from "react-modal";
import { rootUrl } from "..";

const Item = () => {
  const [item, setItem] = useState({});
  const [owner, setOwner] = useState({});
  const [imgUrl, setImgUrl] = useState([]);
  const [modal, setModal] = useState(false);
  const [modalImg, setModalImg] = useState();

  const { id } = useParams();

  const nav = useNavigate();

  //  
  const getItem = async () => {
    const response = fetch(`${rootUrl}/item/${id}`);
    if ((await response).status === 200) {
      (await response).json().then(async (data) => {
        setItem(data.item);
        setImgUrl([...data.item.imgUrl]);
        setOwner(data.item.owner);
      });
    }
  };

  //  
  const ChangeStatus = async () => {
    if (item.owner._id === cookie.load("loggedInUser")) {
      if (window.confirm("  ?")) {
        const response = await fetch(`${rootUrl}/item/${id}/`, {
          method: "put",
        });
        if (response.status === 201) {
          alert("");
          window.scrollTo(0, 0);
          window.location.reload();
        } else {
          alert(" ");
        }
      }
    }
  };

  // 
  const DeleteItem = async () => {
    if (item.owner._id === cookie.load("loggedInUser")) {
      if (window.confirm("?")) {
        const response = await fetch(`${rootUrl}/item/${id}`, {
          method: "delete",
        });
        if (response.status === 201) {
          alert("");
          return nav("/");
        } else {
          return alert(" ");
        }
      } else {
        return alert("");
      }
    }
    alert(" !");
    nav("/");
  };

  //react-slick  
  function NextArrow(props) {
    const { className, style, onClick } = props;
    return (
      <div
        className={className}
        style={{ ...style, display: "block", right: 10 }}
        onClick={onClick}
      />
    );
  }
  function PrevArrow(props) {
    const { className, style, onClick } = props;
    return (
      <div
        className={className}
        style={{ ...style, display: "block", left: 10, zIndex: 1 }}
        onClick={onClick}
      />
    );
  }

  const settings = {
    infinite: true,
    speed: 1000,
    arrows: true,
    // autoplay: true,
    autoplaySpeed: 5000,
    pauseOnHover: true,
    vertical: false,
    slidesToShow: 1,
    slidesToScroll: 1,
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
    dots: true,
    centerMode: true,
    centerPadding: "1px",
  };

  // 
  const onImgClick = (event) => {
    if (modal) {
      setModal(false);
    } else {
      setModalImg(event.target.src);
      setModal(true);
    }
  };
  Modal.setAppElement("#root");

  useEffect(() => {
    if (item?.owner?._id === undefined) return;
    if (owner._id !== cookie.load("loggedInUser")) {
      fetch(`${rootUrl}/item/${id}/count`, {
        method: "PATCH",
      });
    }
  }, [owner]);

  useEffect(() => {
    window.scrollTo(0, 0);
    getItem();
  }, []);

  return (
    <div className={styles.Item}>
      <h1 className={styles.title}>{item.title}</h1>
      <Link to={`/user/${owner._id}`}>
        <h2 className={styles.owner}> | {owner.name}</h2>
      </Link>
      <h2>
         |{" "}
        <span style={{ fontSize: 23, color: item.status ? "red" : "blue" }}>
          {item.status ? "" : ""}
        </span>
      </h2>
      <div className={styles.iteminfo}>
        <div className={styles.sliderContanier}>
          <Modal
            isOpen={modal}
            onRequestClose={() => setModal(false)}
            style={{
              overlay: {
                position: "fixed",
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                backgroundColor: "rgba(255, 255, 255, 0.75)",
                zIndex: 2,
              },
              content: {
                position: "absolute",
                top: "100px",
                left: "40px",
                right: "40px",
                bottom: "40px",
                border: "1px solid #ccc",
                background: "rgb(123, 145, 119)",
                overflow: "auto",
                WebkitOverflowScrolling: "touch",
                borderRadius: "4px",
                outline: "none",
                padding: "20px",
                textAlign: "center",
                verticalAlign: "center",
              },
            }}
          >
            <img
              className={styles.modalImg}
              src={modalImg}
              onClick={onImgClick}
            />
          </Modal>
          <Slider {...settings} className={styles.slider}>
            {item === {}
              ? null
              : imgUrl.map((url, index) => (
                  <div key={index} className={styles.imgs}>
                    <img
                      className={styles.img}
                      src={`http://localhost:4000/${url}`}
                      onClick={onImgClick}
                    />
                  </div>
                ))}
          </Slider>
        </div>
        <div className={styles.description}>
          {item.description === "" ? (
            <p className={styles.noDescript}>
              <br />
              <br />
               
            </p>
          ) : (
            <p>{item.description}</p>
          )}
        </div>
        <div className={styles.tag}>
          {item.hashtags == ""
            ? " "
            : item.hashtags?.length > 1
            ? item.hashtags.map((tag) => `${tag}, `)
            : item.hashtags}
        </div>
        <div className={styles.btns}>
          {item.owner?._id === cookie.load("loggedInUser") ? (
            <div className={styles.ownerBtns}>
              <Link to={`/item/${item?._id}/edit`}>
                <button className={styles.obtn}> </button>
              </Link>
              <button onClick={ChangeStatus} className={styles.obtn}>
                 
              </button>
              <button onClick={DeleteItem} className={styles.obtn}>
                 
              </button>
            </div>
          ) : (
            // <button onClick={() => alert("")} className={styles.btn}>
            //    
            // </button>
            <Link
              to={`/user/${cookie.load("loggedInUser")}/message/${
                owner._id
              }/${id}`}
            >
              <button className={styles.btn}> </button>
            </Link>
          )}
        </div>
      </div>
    </div>
  );
};

export default Item;
