import { useEffect } from "react";
import styles from "../css/Upload.module.css";
import cookie from "react-cookies";
import { rootUrl } from "..";
import { useNavigate } from "react-router-dom";

const Upload = () => {
  const nav = useNavigate();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const onSubmitClick = async (event) => {
    event.preventDefault();
    const title = document.getElementById("title").value;
    const imgs = [...document.getElementById("img").files];
    const description = document.getElementById("description").value;
    const tag = document.getElementById("tag").value;

    if (title === "") {
      return alert(" ");
    } else if (imgs.length === 0) {
      return alert(" ");
    }

    //   base64 
    const incodingImg = [];
    imgs.forEach((img, index) => {
      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");
      const url = URL.createObjectURL(img);
      const image = new Image();
      image.src = url;

      image.onload = () => {
        canvas.width = image.width;
        canvas.height = image.height;
        ctx.drawImage(image, 0, 0);
        //      
        incodingImg.push(
          `${canvas.toDataURL().replace(/^data:image\/(png|jpg);base64,/, "")}`
        );
      };
    });

    // 
    const tags = [...tag.split(",")];

    //       
    setTimeout(async () => {
      const response = await fetch(`${rootUrl}/item/upload`, {
        method: "post",
        headers: {
          "Content-type": "application/json",
        },
        body: JSON.stringify({
          uploader: cookie.load("loggedInUser"),
          title,
          incodingImg,
          description,
          tags,
        }),
      });
      if (response.status !== 201) {
        return alert(" !");
      }
      alert(" !");
      nav("/");
    }, 100);
  };
  return (
    <div className={styles.Upload}>
      <h1 className={styles.title}> </h1>
      <div className={styles.uploadForm}>
        <form>
          <input
            className={styles.postTitle}
            type="text"
            name="tittle"
            id="title"
            placeholder=""
          />

          <input
            className={styles.uploadImg}
            type="file"
            name="img"
            id="img"
            accept="image/*"
            multiple
          />
          <textarea
            className={styles.description}
            name="description"
            id="description"
            maxLength="500"
            cols="30"
            rows="10"
            placeholder="   "
          ></textarea>
          <input
            className={styles.tags}
            type="text"
            name="tags"
            id="tag"
            placeholder=" ,   "
          />
          <input type="submit" onClick={onSubmitClick} value="" />
        </form>
      </div>
    </div>
  );
};

export default Upload;
