import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { rootUrl } from "..";
import styles from "../css/Join.module.css";

const Join = () => {
  const nav = useNavigate();
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const [idExists, setIdexists] = useState(true);
  const [month, setMonth] = useState(0);
  // 
  const onSubmit = async (event) => {
    event.preventDefault();
    if (idExists) {
      return alert("ID  ");
    }
    const name = document.getElementById("name").value;
    const email = document.getElementById("email").value;
    const userId = document.getElementById("id").value;
    const pass = document.getElementById("pass").value;
    const cpass = document.getElementById("cpass").value;
    const year = parseInt(document.getElementById("year").value);
    const day = parseInt(document.getElementById("day").value);
    const tel = document.getElementById("tel").value;

    // 

    // 
    const emailChk = new RegExp("[a-z0-9]+@[a-z]+.[a-z]{2,3}");
    if (!emailChk.test(email)) {
      return alert("email  ");
    }

    // 
    const passChk = new RegExp(
      "^(?=.*[a-z])(?=.*[A-Z])(?=.*d)(?=.*[$@$!%*?&])[A-Za-zd$@$!%*?&]{8,}"
    );
    if (!passChk.test(pass)) {
      return alert(
        "  8 ,   ,  ,       "
      );
    } else {
      if (pass !== cpass) {
        return alert("  ");
      }
    }
    // 
    if (year < 1900 || year > new Date().getFullYear()) {
      return alert(" ");
    }
    switch (true) {
      case month === "2":
        if (parseInt(day) > 29) {
          return alert(" ");
        }
        break;
      case ["1", "3", "5", "7", "8", "10", "12"].includes(month):
        if (parseInt(day) > 31) {
          return alert(" ");
        }
        break;
      case ["4", "6", "9", "11"].includes(month):
        if (parseInt(day) > 30) {
          return alert(" ");
        }
        break;
    }
    const birth = `${year}-${parseInt(month) < 10 ? "0" + month : month}-${
      day < 10 ? "0" + day : day
    }`;

    // 
    const phoneChk = new RegExp("^01([0|1|6|7|8|9])?([0-9]{3,4})?([0-9]{4})$");

    if (!phoneChk.test(tel)) {
      return alert(" ");
    }
    const phone = tel.replace(/^(\d{2,3})(\d{3,4})(\d{4})$/, `$1-$2-$3`);

    //  
    //     

    // 
    const response = await fetch(`${rootUrl}/join`, {
      method: "post",
      headers: {
        "Content-type": "application/json",
      },
      body: JSON.stringify({
        name,
        email,
        userId,
        pass,
        birth,
        phone,
      }),
    });
    if (response.status === 409) {
      alert("  ");
    } else if (response.status === 400) {
      alert("  ");
    } else if (response.status === 201) {
      alert("");
      nav("/login");
    }
  };

  //id 
  const idChk = async () => {
    const id = document.getElementById("id").value;

    if (id === "") {
      return alert("");
    } else if (id.length < 5) {
      return alert("5    ");
    }
    const response = await fetch(`${rootUrl}/join/idcheck?id=${id}`);
    if (response.status === 200) {
      setIdexists(false);
      alert(" ");
    } else {
      setIdexists(true);
      alert("  ");
    }
  };
  return (
    <div className={styles.Join}>
      <h1 className={styles.title}></h1>
      <div className={styles.joinForm}>
        <div className={styles.userInfo}>
          <form>
            <span className={styles.name}>
              
              <br />
              <input type="name" name="name" id="name" required />
            </span>
            <br />
            <span className={styles.email}>
              e-mail
              <br />
              <input type="email" name="email" id="email" required />
            </span>
            <br />
            <span className={styles.id}>
              ID
              <br />
              {/*     */}
              <input
                onChange={() => setIdexists(true)}
                type="text"
                name="id"
                id="id"
              />
              <button onClick={idChk} type="button" className={styles.idChk}>
                ID  
              </button>
            </span>
            <br />
            <span className={styles.pwd}>
               <br />
              <input type="password" name="password" id="pass" required />
              <br />
               
              <br />
              <input type="password" name="pwdCheck" id="cpass" required />
            </span>
            <br />
            <span className={styles.birth}>
              
              <br />
              <input
                type="number"
                name="year"
                id="year"
                placeholder=""
              />{" "}
              -{" "}
              <select
                onChange={(event) => setMonth(event.target.value)}
                name="month"
                id="month"
              >
                <option defaultValue="true"></option>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
                <option value="6">6</option>
                <option value="7">7</option>
                <option value="8">8</option>
                <option value="9">9</option>
                <option value="10">10</option>
                <option value="11">11</option>
                <option value="12">12</option>
              </select>{" "}
              - <input type="number" id="day" placeholder="" required />
            </span>
            <br />
            <span className={styles.tel}>
              <label htmlFor="tel">
                
                <br />
                <input
                  type="text"
                  pattern="[0-9]+"
                  name="tel"
                  id="tel"
                  required
                />
              </label>
            </span>
            <br />
            <input
              onClick={onSubmit}
              type="submit"
              className={styles.joinBtn}
              value=""
            />
          </form>
        </div>
        <br />
        <br />
      </div>
    </div>
  );
};

export default Join;
